"use strict";

var bind = require("../util/bind");
var render = require("../util/render");

var dom = require("ampersand-dom");
var slick = require("slick");
var _ = require("underscore");

var VisitableList = function(app, visitables) {
  this._app = app;
  this._visitables = visitables;
};

VisitableList.prototype = {

  _itemTemplate: "<li style=\"border-left-color: {{color}};\">\n  <div class=\"title\">\n    {{title}}\n  </div>\n  <div class=\"subtitle\">\n    {{subtitle}}\n  </div>\n  <ul class=\"stats\">\n    <li class=\"visits_proportion\">\n      <span class=\"n\">{{visits_proportion}}</span> of all visits\n    </li>\n    <li class=\"visits\">\n      <span class=\"n\">{{visits}}</span> visits\n    </li>\n    <li class=\"views\">\n      <span class=\"n\">{{views}}</span> views\n    </li>\n  </ul>\n</li>\n",

  render: function(selector) {
    var container = slick.find(selector);
    this._element = document.createElement("ul");
    dom.addClass(this._element, "visitable_list");
    container.appendChild(this._element);
    _.each(this._visitables, bind(this, function(visitable) {
      var child = render.toNode(this._itemTemplate, this._templateParameters(visitable));
      this._element.appendChild(child);
    }));
  },

  _templateParameters: function(item) {
    return {
      title: item.title(),
      subtitle: item.subtitle(),
      visits_proportion: (item.visitsProportion() * 100).toFixed(1) + "%",
      visits: item.visits(),
      views: item.views(),
      color: item.color()
    }; 
  }
};

module.exports = VisitableList;
